@import '../../../UI/variables.scss';

.card {
  padding: 16px;
  margin-bottom: 8px;
  background: $color7;
  border-radius: 8px;

  &__title-date {
    display: flex;
    max-width: 350px;
    justify-content: space-between;
    margin-bottom: 11px;
    font-size: 14px;
    line-height: 20 / 14 * 100%;
  }

  &__title {
    font-weight: 600;
  }

  &__date {
    font-weight: 400;
  }

  &__price-img {
    display: flex;
    justify-content: space-between;

    & img {
      width: 32px;
      height: 32px;
    }
  }

  &__price {
    font-weight: 700;
    font-size: 22px;
    line-height: 27 / 22 * 100%;
    color: $color2;
  }
}