@import '../../UI/variables.scss';

.form {
  width: 480px;
  height: 427px;

  @media (max-width: 490px){
      width: 300px;
  }

  &__title {
    padding-bottom: 24px;
    text-align: center;
    font-size: 18px;
    line-height: 18 / 22 * 100%;
    color: #beb8e0;
    text-transform: uppercase;
  }

  &__background {
    padding: 32px 24px 36px 24px;
    margin-bottom: 24px;
    border-radius: 8px;
    background-color: $color6;
    box-shadow: 0px 8px 12px 0px rgba(18, 33, 54, 0.05);
  }

  * &__flex:not(:last-child) {
    margin-bottom: 32px;

    @media (max-width: 490px){
      margin-bottom: 20px;
    }
  }

  &__flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
    position: relative;
  }

  &__flex label {
    position: absolute;
    left: 0%;
    top: 50%;
    transform: translate(0, -50%);
    line-height: (19 / 16) * 100%;

    @media (max-width: 490px){
      font-size: 15px;
    }
  }

  &__flex p {
    position: absolute;
    width: 100%;
    bottom: -17px;
    left: 0;
    text-align: left;
    font-weight: 400;
    font-size: 12px;
    line-height: 15 / 12 * 100%;
    color: $color5;
  }

  &__flex div {
    position: relative;
  }

  & input,
  select {
    width: 333px;
    height: 36px;
    padding: 8px;
    border: 1px solid $color3;
    border-radius: 4px;

    @media (max-width: 490px){
        width: 160px;
    }
  }

  & input[type=date]:required:invalid::-webkit-datetime-edit {
    color: transparent;
}
  & input[type=date]:focus::-webkit-datetime-edit {
    color: $color1 !important;
  }

  & input[type=date]::-webkit-calendar-picker-indicator {
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="15" viewBox="0 0 24 24"><path fill="%23CADEFC" d="M20 3h-1V1h-2v2H7V1H5v2H4c-1.1 0-2 .9-2 2v16c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 18H4V8h16v13z"/></svg>');
  }

  &__flex.invalid input,
  &__flex.invalid select {
    border: 1px solid $color5;
  }

  & select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
}

.btns {
  display: flex;
  justify-content: space-between;

  & button {
    width: 232px;
    height: 53px;
    font-size: 16px;
    font-weight: 800;
    box-shadow: 0px 8px 12px 0px rgba(18, 33, 54, 0.05);
    border-radius: 4px;

    @media (max-width: 490px){
        width: 140px;
    }
  }

  &__cancel {
    background-color: $color6;
    color: $color2;
  }

  &__submit {
    background-color: $color2;
    color: $color7;
  }
}
