@import '../../../UI/variables.scss';
.bar {
  border-radius: 2px;
  margin-right: 16px;
  
  @media (max-width: 490px){
    margin-right: 5px;
  }

  &__background {
    margin-left: 4px;
    position: relative;
    width: 12px;
    height: 171px;
    background: $color7;
  }

  &__color {
    position: absolute;width: 100%;bottom: 0;left: 0;
    z-index: 4;
    width: 12px;
    background-color: $color4;
    transition: all 0.3s ease 0s;
  }

  &__title {
    font-weight: 400;
    font-size: 12px;
    line-height: 15 / 12 * 100%;
    text-align: center;

    @media (max-width: 490px){
      font-size: 10px;
    }
  }
}


