@import '../UI/variables.scss';

.filter {
  &__handler-body {
    background-color: $color6;
    width: 480px;
    padding: 29px 18px 16px 24px;

    @media (max-width: 490px){
      width: 300px;
      padding: 29px 15px 16px 15px;
    }

    & label {
      min-width: 124px;
      padding: 7px 16px 10px 8px;
      line-height: 19 / 16 * 100%;
    }

    & select {
      font-size: 16px;
      font-weight: 800;
      font-family: 'Inter';
    }
  }

  &__title-select {
      margin-bottom: 48px;
      display: flex;
      justify-content: space-between;
      width: 230px;
      margin-left: auto;
      margin-right: auto;

      @media (max-width: 490px){
        margin-bottom: 25px;
      }
    }
}

.select-wrapper {
  width: 106px;
  height: 36px;
  position: relative;
  padding: 9px 14px 0 24px;
  border: 1px solid $color3;
  border-radius: 4px;

  & > select {
    width: 100%;
    background: transparent;
    border: 0;
    appearance: none;
    z-index: 1;
    -webkit-appearance: none;
    -moz-appearance: none;
  }

  & > select::-ms-expand{
    display: none;
}

}