@import '../UI/variables.scss';

.expense {
  background-color: #F7F9FD;
  margin-bottom: 24px;

    &__noneExpanded button{
      width: 480px;
      height: 53px;

      @media (max-width: 490px){
        width: 300px;
      }
    }

    &__btn {
      width: 232px;
      height: 53px;
      font-size: 16px;
      font-weight: 800;
      box-shadow: 0px 8px 12px 0px rgba(18, 33, 54, 0.05);
      background-color: $color2;
      color: $color6;
    }
}